import React, { useState } from "react";
import Input from "../Input";
import style from "./MultiDropdown.module.scss";
import classNames from "classnames";
import { useClickOutside } from "hooks/useClickOutside.ts";
import { motion, AnimatePresence } from "framer-motion";

export type Option = {
  /**  ,     /   */
  key: string | number;
  /**  ,   */
  value: string;
};

/** ,    Dropdown */
export type MultiDropdownProps = {
  className?: string;
  /**      */
  options: Option[];
  /**    ,    */
  value: Option[];
  /** Callback,     */
  onChange: (value: Option[]) => void;
  /**    */
  disabled?: boolean;
  /**       .      ,     placeholder. */
  getTitle: (value: Option[]) => string;
};

const MultiDropdown: React.FC<MultiDropdownProps> = ({
  options,
  value,
  onChange,
  getTitle,
  className,
  disabled,
}) => {
  const [inputValue, setInputValue] = useState("");
  const [isDropdownOpen, setIsDropdownOpen] = useState(false);
  const dropdownContainerRef = useClickOutside(() => setIsDropdownOpen(false));

  //     
  const filteredOptions = options.filter((option) =>
    option.value.toLowerCase().includes(inputValue.toLowerCase()),
  );

  //   
  const handleOptionClick = (option: Option) => () => {
    const isSelected = value.some((selected) => selected.key === option.key);
    onChange(isSelected ? [] : [option]); //   
    setIsDropdownOpen(false); //  dropdown  
  };

  const handleOnFocus = () => {
    setIsDropdownOpen(true);
  };
  //       getTitle (  ),
  //     ( dropdown )
  const inputDisplayValue =
    !isDropdownOpen && value.length > 0 ? getTitle(value) : inputValue;

  return (
    <div
      ref={dropdownContainerRef}
      className={classNames(className, style["multiDropdown"])}
    >
      <Input
        value={inputDisplayValue}
        placeholder={getTitle(value)}
        onChange={setInputValue}
        onFocus={handleOnFocus}
        disabled={disabled}
        afterSlot={true}
      />
      <AnimatePresence>
        {isDropdownOpen && !disabled && (
          <motion.div
            initial={{
              opacity: 0,
              scaleY: 0,
              transformOrigin: "top",
            }}
            animate={{
              opacity: 1,
              scaleY: 1,
              transformOrigin: "top",
            }}
            exit={{
              opacity: 0,
              scaleY: 0,
              transformOrigin: "top",
            }}
            transition={{
              duration: 0.3,
              ease: "easeIn",
            }}
            className={style.dropdown}
          >
            {filteredOptions.map((option) => {
              const isSelected = value.some(
                (selected) => selected.key === option.key,
              );
              return (
                <div
                  key={option.key}
                  onClick={handleOptionClick(option)}
                  className={classNames(style.option, {
                    [style.optionActive]: isSelected,
                  })}
                >
                  {option.value}
                </div>
              );
            })}
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
};

export default MultiDropdown;
