export function getTovarEnding(count: number): string {
  const lastTwoDigits = Math.abs(count) % 100;
  const lastDigit = lastTwoDigits % 10;

  if (lastTwoDigits >= 11 && lastTwoDigits <= 14) {
    return "";
  }

  switch (lastDigit) {
    case 1:
      return "";
    case 2:
    case 3:
    case 4:
      return "";
    default:
      return "";
  }
}
